import { Box, Stack, Text } from '@chakra-ui/react'
import { Image } from '@chakra-ui/react'
// import ProfilePic from '../Images/ProfilePic.jpg';
import Profile5 from '../Images/Profile5.jpg';
import AboutMeImage from '../Images/AboutMeImage.avif'
import htmlLogo from '../Images/htmlLogo.gif'
import CssLogo from '../Images/CssLogo.jpg'
import HtmlLogo from '../Images/HtmlLogo.jpg'
import JsLogo from '../Images/JsLogo.jpg'
import { ImageComp } from '../Components/ImageComp';



import styled from 'styled-components'
import { SkillsComp } from '../Components/SkillsComp';



function Home() {

    return <DIV id="home">

        <div style={{ border: "2px solid yellow", width: "80%", margin: "auto", display: "flex", justifyContent: "space-evenly" }}>
            <div style={{ border: "2px solid green", fontFamily: "Poppins,sans-serif", fontWeight: "bold" }}>
                <Stack spacing={3}>

                    <div style={{ display: "flex" }}>
                        <Text fontSize='5xl'>Hi,</Text>
                        <div style={{ width: "13%", }}>
                            <img style={{ width: "100%", borderRadius: "40px" }} src="https://c.tenor.com/z2xJqhCpneIAAAAM/wave-hand.gif" alt="" />
                        </div>
                    </div>

                    <Text fontSize='5xl'>I'm Parmeshwar Murmu</Text>


                    {/* <Text fontSize='5xl'>Hi, I'm Parmeshwar Murmu</Text> */}

                    <Text fontSize='2xl'>FULL STACK WEB DEVELOPER</Text>

                    {/* <Text fontSize='1xl' >I am from Jharkhand, Ghatshila</Text>

                    <Text fontSize='1xl' style={{margin: "0px"}}>edfdf</Text>
                    <Text fontSize='1xl'>FULL STACK WEB DEVELOPER</Text> */}
                    <p style={{ margin: "0px", padding: "0px", fontWeight: "bolder" }}>I am from Jharkhand, Ghatshila</p>
                    <p style={{ margin: "0px", padding: "0px" }}>Phone - +91 9934524961</p>
                    <p></p>



                </Stack>


            </div>

            <div style={{ border: "2px solid red" }}>
                <Image
                    borderRadius='full'
                    boxSize='300px'
                    src={Profile5}
                    alt='Parmeshwar Murmu'
                />
            </div>
        </div>

        {/* ABOUT ME SECTION */}


        <div style={{ border: "2px solid black", width: "80%", margin: "auto" }}>
            <Text fontSize='4xl' style={{ textAlign: "center", textDecoration: "underline" }}>About Me</Text>

            <div style={{ backgroundColor: "#5a5f69", textAlign: "center", padding: "15px" }}>

                <div style={{ width: "20%", margin: "auto" }}>

                    <ImageComp size={""} name={"ParmeshwarMurmu"} src={AboutMeImage} boxSize={'200px'} boderRadius={"full"} />


                </div>
                <p>Skilled Full Stack Web Developer proficient in MERN stack,  Demonstrated expertise in HTML, CSS, JavaScript, React, Node.js, and MongoDB. Hands-on experience in executing projects, emphasizing attention to detail and problem-solving abilities. A collaborative team player committed to delivering impactful solutions.</p>

            </div>
        </div>




        {/* SKILLS SECTION  */}


        <div style={{ border: "2px solid black", width: "80%", margin: "auto" }}>
            <Text fontSize='4xl' style={{ textAlign: "center", textDecoration: "underline" }}>Skills</Text>

            <div style={{ backgroundColor: "#5a5f69", textAlign: "center", padding: "15px" }}>

                <div style={{ display: "flex", justifyContent: "space-between" }}>

                    <div className='skillsLogo'>

                        
                        <SkillsComp imgSrc={HtmlLogo} />
                        <SkillsComp imgSrc={CssLogo} />
                        <SkillsComp imgSrc={JsLogo} />

                    </div>

                    <div style={{ backgroundColor: "blue", width: "50%" }}>

                        <ImageComp size={""} name={"ParmeshwarMurmu"} src={AboutMeImage} boxSize={'200px'} boderRadius={"full"} />


                    </div>

                </div>


            </div>
        </div>

    </DIV>

}

export default Home;

const DIV = styled.div`

.skillsLogo{
    /* background-color: green; */
    width: 50%;
    display: flex;
    justify-content: space-evenly;
}
    
`