import { Box, SimpleGrid, Stack } from '@chakra-ui/react'
import { Text } from '@chakra-ui/react'
import { styled } from "styled-components"
import CssLogo from '../Images/CssLogo.jpg'
import HtmlLogo from '../Images/HtmlLogo.jpg'
import JsLogo from '../Images/JsLogo.jpg'


function Skills() {

    return <DIV id="skills" className='skills'>

        <SimpleGrid minChildWidth='300px' spacing='40px'>



            <Box height='80px' className='box'>

                <div style={{ width: "18%" }}>
                    <img src={HtmlLogo} alt="" />
                </div>

                <div >
                    <Stack spacing={3}>
                        
                        <Text fontSize='4xl'>HTML</Text>
                        
                    </Stack>
                </div>
            </Box>



            <Box height='80px' className='box'>

                <div style={{ width: "18%" }}>
                    <img src={CssLogo} alt="" />
                </div>

                <div >
                    <Stack spacing={3}>
                        
                        <Text fontSize='4xl'>CSS</Text>
                        
                    </Stack>
                </div>
            </Box>

            <Box height='80px' className='box'>

                <div style={{ width: "18%" }}>
                    <img src={JsLogo} alt="" />
                </div>

                <div >
                    <Stack spacing={3}>
                        
                        <Text fontSize='4xl'>JavaScript</Text>
                        
                    </Stack>
                </div>
            </Box>

            <Box height='80px' className='box'>

                <div style={{ width: "18%" }}>
                    <img src={HtmlLogo} alt="" />
                </div>

                <div >
                    <Stack spacing={3}>
                        
                        <Text fontSize='4xl'>HTML</Text>
                        
                    </Stack>
                </div>
            </Box>


            <Box height='80px' className='box'>

                <div style={{ width: "18%" }}>
                    <img src={HtmlLogo} alt="" />
                </div>

                <div >
                    <Stack spacing={3}>
                        
                        <Text fontSize='4xl'>HTML</Text>
                        
                    </Stack>
                </div>
            </Box>


            <Box height='80px' className='box'>

                <div style={{ width: "18%" }}>
                    <img src={HtmlLogo} alt="" />
                </div>

                <div >
                    <Stack spacing={3}>
                        
                        <Text fontSize='4xl'>HTML</Text>
                        
                    </Stack>
                </div>
            </Box>


        </SimpleGrid>

    </DIV>

}

export default Skills;

const DIV = styled.div`
background-color: gray;
padding: 20px;

.box{
    display: flex;
    justify-content: space-around;
    align-items: center;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
    background-color: white;
}



`